import { mapState } from "vuex";
// Import component
import Loading from "vue-loading-overlay";
// Import stylesheet
import "vue-loading-overlay/dist/vue-loading.css";

export default {
  data: () => ({
    months: [],
    monthsArray: [],
    station: null,
    isLoading: false,
    fullPage: true,
  }),
  computed: {
    ...mapState({
      stations: (state) => state.stations,
    }),
  },
  components: {
    Loading,
  },
  methods: {
    download: function (month, station) {
      if (month.length < 1) {
        alert("Please select month");
        return;
      }
      if (station == null) {
        alert("Please select jetty");
        return;
      }
      // this.setLoading1(true);
      this.isLoading = true;
      this.$axios
        .$get(`http://167.99.75.232:7777/api/download/month`, {
          // .$get(`http://localhost:5000/api/download/month`, {
          params: {
            // .$get(`/api/daily/`,{params:{
            station: station,
            month: month,
          },
        })
        .then((response) => {
          console.log(response);
          // res.download(response);
          if (typeof response !== "object") {
            let blob = new Blob([response], { type: "text/csv" });
            let link = document.createElement("a");
            link.href = window.URL.createObjectURL(blob);
            link.download = "Report.csv";
            link.click();
            this.isLoading = false;
          } else {
            alert(response.msg);
            this.isLoading = false;
          }
        })
        .catch((error) => {
          console.log(error);
          alert(error);
          this.isLoading = false;
        });
    },
    arrange: function () {
      var sorted = this.months.sort(function (date1, date2) {
        return new Date(date1) - new Date(date2);
      });
      this.monthsArray = sorted;
      console.log(this.monthsArray);
    },
    console: function (index) {
      // console.log(index);
      const active = document.getElementById(index).classList.add("active");
      for (let i = 0; i <= 4; i++) {
        if (i != index) {
          let deactive = document.getElementById(i).classList.remove("active");
        }
      }
      let station = [
        "geyser_one",
        "geyser_two",
        "geyser_four",
        "geyser_three",
        "geyser_five",
      ];
      let sensorArray = [];
      this.station = station[index];
      console.log(this.station);
    },
    clear: function () {
      for (let i = 0; i <= 4; i++) {
        // if (i != index) {
        let deactive = document.getElementById(i).classList.remove("active");
        // }
      }
    },
  },
};
